export function useWorker(type, ...args) {
    const worker = new Worker("./build/worker.js");
    worker.postMessage({
        type: type,
        args: args
    });
    return new Promise((resolve, reject) => {
        worker.onmessage = (e) => {
            resolve(e.data);
            worker.terminate();
        };
    });
}
export class DevelopmentError extends Error {
    constructor(message) {
        super(message);
        this.name = "DevelopmentError";
    }
}
export class UserError extends Error {
    constructor(message) {
        super(message);
        this.name = "UserError";
    }
}
