import { useWorker } from "../../Utils";
export function modularExponentiation(x, y, p) {
    return useWorker("modularExponentiation", x, y, p);
}
export function moduloGenerator(base, mod) {
    return useWorker("moduloGenerator", base, mod);
}
export function isPrimitiveRoot(generatedModulo) {
    return useWorker("isPrimitiveRoot", generatedModulo);
}
export function primitiveRootOf(mod) {
    return useWorker("primitiveRootOf", mod);
}
export function discreteLogarithm(root, mod, value) {
    return useWorker("discreteLogarithm", root, mod, value);
}
