import { useWorker } from "../../Utils";
export function fibonacci(length) {
    return useWorker("fibonacci", length);
}
export function arithmeticSum(from, step, length) {
    return (length / 2) * (from * 2 + ((length - 1) * step));
}
export function arithmeticSumWithLastTerm(from, to, length) {
    return (length / 2) * (from + to);
}
export function arithmeticNthTerm(from, step, length) {
    return from + ((length - 1) * step);
}
export function geometricSum(from, ratio, length) {
    if (Math.abs(ratio) < 1)
        return (from * (1 - (ratio ** length))) / (1 - ratio);
    else
        return (from * ((ratio ** length) - 1)) / (ratio - 1);
}
export function infiniteGeometricSum(from, ratio) {
    return from / (1 - ratio);
}
export function geometricNthTerm(from, ratio, length) {
    return from * (ratio ** (length - 1));
}
export function pascalTriangle(from, to) {
    return useWorker("pascalTriangle", from, to);
}
