import { BackendUtils, UserError } from "../../Utils/BackEndUtils";
export class Converter {
    constructor(onUpdate, onError) {
        this.fromType = "decimal";
        this.toType = "decimal";
        this.isTwoComplement = false;
        this.onUpdate = onUpdate;
        this.onError = onError;
    }
    swap() {
        const temp = this.from;
        this.from = this.to;
        this.to = temp;
    }
    indexOfType(type) {
        return Converter.TYPES.indexOf(type);
    }
    convert() {
        let temp;
        try {
            temp = parseInt(this.from, Converter.BASES[this.indexOfType(this.fromType)]);
            if (isNaN(temp))
                throw new UserError(Converter.ERROR_MESSAGES[this.indexOfType(this.fromType)]);
        }
        catch (e) {
            BackendUtils.errorHandling(e, this.onError);
            return;
        }
        if (this.isTwoComplement && temp < 0) {
            let subtracter;
            temp = Math.abs(temp);
            for (let bitcount of Converter.BITSTEPS) {
                if (bitcount > temp) {
                    subtracter = bitcount;
                    break;
                }
            }
            if (!subtracter) {
                this.onError("Two's Complement is only supported up to 64-bit numbers!");
                return;
            }
            temp = subtracter - temp;
        }
        this.to = temp.toString(Converter.BASES[this.indexOfType(this.toType)]);
    }
}
Converter.TYPES = ["binary", "octal", "decimal", "hexadecimal"];
Converter.BASES = [2, 8, 10, 16];
Converter.BITSTEPS = [2 ** 8, 2 ** 16, 2 ** 32, 2 ** 64];
Converter.ERROR_MESSAGES = ["Binary strings must only use 1s or 0s!", "Octal numbers should only have digits between 0-7!", "Decimal numbers should only digits between 0-9!", "Hexadecimal numbers should only contain digits from 0-9 or characters between a-f!"];
